.footer-section {
  background: whitesmoke;
  position: relative;
}

.footer-cta {
  border-bottom: 1px solid #373636;
  padding-bottom: 8px !important;
}

.single-cta i {
  color: #ff5e14;
  font-size: 30px;
  float: left;
  margin-top: 8px;
}

.cta-text {
  padding-left: 15px;
  display: inline-block;
}

.cta-text h4 {
  color: #000000;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 2px;
}

.cta-text span {
  color: #161616;
  font-size: 15px;
}

.footer-content {
  position: relative;
  z-index: 2;
  text-align: center;
}

.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%;
}

.footer-logo img {
  width: 220px;
  margin-bottom: 20px;

}

.footer-text p {
  margin-bottom: 14px;
  font-size: 20px;
  color: #3a3939;
}

.footer-social-icon span {
  color: #000000;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  margin-bottom: 20px;
}

.footer-social-icon a {
  color: #000000;
  font-size: 16px;
  margin-right: 15px;
}

.footer-social-icon i {
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 50%;
}

.facebook-bg {
  background: #3b5998;
}

.twitter-bg {
  background: #55acee;
}

.google-bg {
  background: #dd4b39;
}

.footer-widget-heading h3 {
  color: #000000;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
}

.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background: #ff5e14;
}

.footer-widget ul li {
  display: inline-block;
  float: left;
  width: 50%;
  margin-bottom: 12px;
}

.footer-widget ul li a:hover {
  color: #ff5e14;
}

.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
}

.subscribe-form {
  position: relative;
  overflow: hidden;
}

.subscribe-form input {
  width: 100%;
  padding: 14px 28px;
  background: #2e2e2e;
  border: 1px solid #2e2e2e;
  color: #000000;
}

.subscribe-form button {
  position: absolute;
  right: 0;
  background: #ff5e14;
  padding: 13px 20px;
  border: 1px solid #ff5e14;
  top: 0;
}

.subscribe-form button i {
  color: #000000;
  font-size: 22px;
  transform: rotate(-6deg);
}

.copyright-area {
  background: rgb(220, 220, 220);
  padding: 25px 0;
  margin-top: 50px;
  text-align: center;
}

.copyright-text p {
  margin: 0;
  font-size: 18px;
  color: #5e5d5d;
  text-align: center;
}

.copyright-text p a {
  color: #0b2847;
}

.footer-menu li {
  display: inline-block;
  margin-left: 20px;
}

.footer-menu li:hover a {
  color: #ff5e14;
}

.footer-menu li a {
  font-size: 14px;
  color: #878787;
}

.footer-cta .row .col-xl-4:nth-child(1) .single-cta i {
  position: absolute;
  color: #ff5e14;
}

.footer-cta .row .col-xl-4:nth-child(1) .single-cta .cta-text {
  padding-left: 32px;
}

.footer-cta .row .col-xl-4:nth-child(2) .single-cta i {
  color: #14ffa7;
}

.footer-cta .row .col-xl-4:nth-child(3) .single-cta i {
  color: #ffd700;
}

.footer-social-icon .social_icon {
  text-align: left;
}

.footer-social-icon .social_icon li {
  float: inherit !important;
  display: inline-block !important;
  width: inherit;
}

.footer-social-icon .social_icon li a i {
  font-size: 15px;
  line-height: 39px;
}

.footer-social-icon .social_icon li a {
  width: 40px;
  height: 40px;
}

.social_icon {
  text-align: right;
  margin: 0;
  margin-top: 3px;
}

.social_icon li {
  display: inline-block;
  margin: 0 5px;
}

.social_icon li a {
  display: inline-block;
  width: 25px;
  height: 25px;
  background: #fff;
  color: #000;
  text-align: center;
  border-radius: 100%;
  transition: 0.2s ease-in-out;
}

.social_icon li a i {
  font-size: 15px;
  line-height: 20px;
}

.social_icon li:nth-child(1) a {
  background: #4267b2;
  color: #fff;
}

.social_icon li:nth-child(2) a {
  background: #1da1f2;
  color: #fff;
}

.social_icon li:nth-child(3) a {
  background: #f09433;
  background: -moz-linear-gradient(45deg,
      #f09433 0%,
      #e6683c 25%,
      #dc2743 50%,
      #cc2366 75%,
      #bc1888 100%);
  background: -webkit-linear-gradient(45deg,
      #f09433 0%,
      #e6683c 25%,
      #dc2743 50%,
      #cc2366 75%,
      #bc1888 100%);
  background: linear-gradient(45deg,
      #f09433 0%,
      #e6683c 25%,
      #dc2743 50%,
      #cc2366 75%,
      #bc1888 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f09433', endColorstr='#bc1888', GradientType=1);
  color: #fff;
}

.social_icon li:nth-child(4) a {
  background: #ff0000;
  color: #fff;
}

.social_icon li:nth-child(1):hover a {
  background: #fff;
  color: #4267b2;
}

.social_icon li:nth-child(2):hover a {
  background: #fff;
  color: #1da1f2;
}

.social_icon li:nth-child(3):hover a {
  background: #fff;
  color: #bc1888;
}

.social_icon li:nth-child(4):hover a {
  background: #fff;
  color: #ff0000;
}



.fa {
  /* padding: 20px; */
  font-size: 20px;
  align-items: center;
  /* display: flex; */
  width: 50px;
  height: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  line-height: 50px;
  border-radius: 40px;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-twitter {
  background: #55ACEE;
  color: white;
}

.fa-google {
  background: #dd4b39;
  color: white;
}